import i18n from "@/lang/i18n";

const { t, te } = i18n.global;

class Errors {
  /**
   * Create a new Errors instance.
   */
  constructor() {
    this.errors = {};
    this.status = null;
    this.message = "";
  }

  /**
   * Determine if an errors exists for the given field.
   *
   * @param {string} field
   */
  has(field) {
    for (let key in this.errors) {
      if (key === field) {
        return true;
      }
    }

    return false;
  }

  /**
   * Determine if we have any errors.
   */
  any() {
    return (
      this.errors !== null &&
      this.errors !== undefined &&
      Object.keys(this.errors).length > 0
    );
  }

  /**
   * Retrieve the error message for a field.
   *
   * @param {string} field
   */
  get(field) {
    if (this.errors[field]) {
      return this.errors[field][0];
    }
  }

  /**
   * Record the new errors.
   *
   * @param {object} response
   */
  record(response) {
    this.errors = response.errors !== undefined ? response.errors : {};
    this.message = response.message !== undefined ? response.message : "";
  }

  /**
   * Record the status code.
   *
   * @param {number} status
   */
  setStatus(status) {
    this.status = status;
  }

  /**
   * Clear one or all error fields.
   *
   * @param {string|null} field
   */
  clear(field = null) {
    if (field) {
      delete this.errors[field];

      if (!this.errors.length) {
        this.message = "";
      }

      return;
    }

    this.errors = {};
    this.status = null;
    this.message = "";
  }

  /**
   * Get response message.
   */
  getMessage() {
    if (this.status !== null && te("response.errors." + this.status)) {
      return t("response.errors." + this.status);
    }

    return this.message;
  }
}

export default Errors;
