<template>
  <breadcrumb-list :items="breadcrumbs"></breadcrumb-list>

  <div class="grid grid-cols-12 gap-4 sm:gap-5 lg:gap-6">
    <div class="col-span-12 xl:col-span-8">
      <div class="p-4 card sm:p-5">
        <form
          @input="form.clearError($event.target.name)"
          @submit.prevent="submit()"
        >
          <div class="space-y-4">
            <div>
              <label for="textName">{{ $t("attributes.name") }}</label>
              <span class="relative flex mt-1.5">
                <input
                  v-model="form.name"
                  id="textName"
                  name="name"
                  type="text"
                  class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 pl-9 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                />
                <span
                  class="pointer-events-none absolute flex h-full w-10 items-center justify-center text-slate-400 peer-focus:text-primary dark:peer-focus:text-accent dark:text-navy-300"
                >
                  <i class="text-base fa-regular fa-hospital"></i>
                </span>
              </span>

              <validation-feedback>
                {{ form.getError("name") }}
              </validation-feedback>
            </div>

            <div>
              <label for="selectCity">{{ $t("attributes.city") }}</label>
              <div class="mt-1 w-full">
                <vue-select
                  :options="cities"
                  label="name"
                  :reduce="(city) => city.id"
                  v-model="form.city_id"
                  inputId="selectCity"
                  :placeholder="$t('placeholder.select_city')"
                  @update:modelValue="citySelected"
                ></vue-select>
              </div>

              <validation-feedback>
                {{ form.getError("city_id") }}
              </validation-feedback>
            </div>

            <div>
              <label for="selectCounty">{{ $t("attributes.county") }}</label>
              <div class="mt-1 w-full">
                <vue-select
                  :options="counties"
                  label="name"
                  :reduce="(county) => county.id"
                  v-model="form.county_id"
                  inputId="selectCounty"
                  :disabled="!(counties.length > 0)"
                  :placeholder="
                    form.city_id
                      ? $t('placeholder.select_county')
                      : $t('placeholder.select_city')
                  "
                  @update:modelValue="countySelected"
                ></vue-select>
              </div>

              <validation-feedback>
                {{ form.getError("county_id") }}
              </validation-feedback>
            </div>

            <div class="text-end">
              <v-button
                type="submit"
                color="primary"
                :disabled="!form.isSubmittable()"
              >
                {{ $t("labels.submit") }}
              </v-button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/core/Api";
import Form from "@/core/Form";
import Toast from "@/core/Toast";

export default {
  name: "HospitalForm",
  props: {
    id: {
      type: String,
      required: false,
    },
  },
  data() {
    return {
      breadcrumbs: [
        { title: this.$t("titles.hospitals"), route: "hospitals" },
        {
          title: this.id
            ? this.$t("models.hospital.edit")
            : this.$t("models.hospital.add"),
          active: true,
        },
      ],
      cities: [],
      form: new Form({
        name: "",
        city_id: null,
        county_id: null,
      }),
    };
  },
  async created() {
    await api.get("/cities").then(({ data }) => {
      this.cities = data;
    });

    if (this.id) {
      await api.get(`/hospital/${this.id}`).then(({ data }) => {
        this.form.updateData(data);
      });
    }
  },
  methods: {
    citySelected() {
      this.form.county_id = null;
      this.form.clearError("city_id");
      this.form.clearError("county_id");
    },
    countySelected() {
      this.form.clearError("county_id");
    },
    submit() {
      if (this.form.isUpdate()) {
        this.form
          .post("/hospital/update", this.form.getId())
          .then(() => {
            Toast.fire(this.$t("response.success.update"), "success");
          })
          .catch(() => {
            Toast.fire(this.form.getErrorMessage(), "error");
          });
      } else {
        this.form
          .post("/hospital/store")
          .then(() => {
            Toast.fire(this.$t("response.success.store"), "success");
          })
          .catch(() => {
            Toast.fire(this.form.getErrorMessage(), "error");
          });
      }
    },
  },
  computed: {
    counties() {
      if (this.form.city_id) {
        const city = this.cities.find(({ id }) => {
          return this.form.city_id === id;
        });

        return city.counties;
      }

      return [];
    },
  },
};
</script>
