import api from "./Api";
import Errors from "./Errors";
import { serialize } from "object-to-formdata";

class Form {
  /**
   * Create a new Form instance.
   *
   * @param {object} data - The initial data for the form.
   */
  constructor(data) {
    this.$data = data;
    this.$errors = new Errors();
    this.$canSubmit = true;
    this.$multipart = false;
    this.$json = false;
    this.$id = data["id"] ?? null;

    for (let field in data) {
      this[field] = data[field];
    }
  }

  /**
   * Delete old fields and set new data.
   *
   * @param {object} newData - The new data to set.
   */
  setData(newData) {
    for (let field in this.$data) {
      delete this[field];
    }

    this.$data = newData;

    for (let field in newData) {
      this[field] = newData[field];
    }

    this.$id = newData["id"] ?? null;

    this.clearError();
  }

  /**
   * Update data by fields.
   *
   * @param {object} newData - The new data to update.
   * @param {boolean} updateId - State of the update ID.
   */
  updateData(newData, updateId = true) {
    for (let field in this.$data) {
      this.$data[field] = newData[field];
      this[field] = newData[field];
    }

    if (updateId) {
      this.$id = newData["id"] ?? null;
    }

    this.clearError();
  }

  /**
   * Get the data id.
   *
   * @returns {number|null} - The data id.
   */
  getId() {
    return this.$id;
  }

  /**
   * Delete the form fields.
   */
  deleteData() {
    for (let field in this.$data) {
      delete this[field];
    }

    this.$data = {};
  }

  /**
   * Set files to be sent.
   *
   * @param {string} key - The key to set the files.
   * @param {File[]|object} files - The files to set.
   * @param {boolean} [multiple=false] - Whether multiple files can be set or not.
   * @returns {boolean} - Returns true if the files are set successfully, false otherwise.
   */
  setFile(key, files, multiple = false) {
    if (typeof files === "object") {
      this.$multipart = true;

      if (multiple) {
        this[key] = files;

        return true;
      }

      if (files[0] instanceof File) {
        this[key] = files[0];

        return true;
      }
    }

    return false;
  }

  /**
   * Get the request headers.
   *
   * @returns {object} - The headers object.
   */
  getHeaders() {
    let headers = {};

    if (this.$multipart) {
      headers["Content-Type"] = "multipart/form-data";
    }

    return headers;
  }

  /**
   * Fetch all relevant data.
   *
   * @returns {object} - The form data object.
   */
  getData() {
    let data = {};

    for (let field in this.$data) {
      data[field] = this[field];
    }

    return data;
  }

  /**
   * Fetch all relevant data for the form.
   *
   * @returns {FormData} - The form data object.
   */
  formData() {
    let serializeOptions = {
      indices: true,
      nullsAsUndefineds: true,
      booleansAsIntegers: true,
      allowEmptyArrays: true,
    };

    return serialize(this.getData(), serializeOptions);
  }

  /**
   * Fetch all relevant data for the form.
   *
   * @returns {object} - The form data object.
   */
  jsonData() {
    let data = {};

    for (let field in this.$data) {
      if (this[field] !== null && typeof this[field] !== "undefined") {
        data[field] = this[field];
      }
    }

    return data;
  }

  /**
   * Add specific data to the form.
   *
   * @param {string} field - The field name.
   * @param {any} value - The value to set.
   */
  addData(field, value) {
    if (this[field]) {
      this[field] = value;

      return;
    }

    this[field] = value;
    this.$data[field] = value;
  }

  /**
   * Check if the form is submittable.
   *
   * @returns {boolean} - Returns true if the form is submittable, false otherwise.
   */
  isSubmittable() {
    return this.$canSubmit;
  }

  /**
   * Check if the form is in update mode.
   *
   * @returns {boolean} - Returns true if the form is in update mode, false otherwise.
   */
  isUpdate() {
    return this.$id !== null;
  }

  /**
   * Set the form to use JSON format for data.
   *
   * @returns {this} - Returns the Form instance.
   */
  json() {
    this.$json = true;
    return this;
  }

  /**
   * Check if the form is using JSON format for data.
   *
   * @returns {boolean} - Returns true if the form is using JSON format, false otherwise.
   */
  isJson() {
    return this.$json;
  }

  /**
   * Send a POST request to the given URL.
   *
   * @param {string} url - The URL to send the request to.
   * @param {number|null} id - The optional ID parameter for the URL.
   */
  post(url, id = null) {
    if (id) {
      return this.submit("post", `${url}/${id}`);
    }

    return this.submit("post", url);
  }

  /**
   * Send a PUT request to the given URL.
   *
   * @param {string} url - The URL to send the request to.
   * @param {number|null} id - The optional ID parameter for the URL.
   */
  put(url, id = null) {
    if (id) {
      return this.submit("put", `${url}/${id}`);
    }

    return this.submit("put", `${url}`);
  }

  /**
   * Send a PATCH request to the given URL.
   *
   * @param {string} url - The URL to send the request to.
   * @param {number|null} id - The optional ID parameter for the URL.
   */
  patch(url, id = null) {
    if (id) {
      return this.submit("patch", `${url}/${id}`);
    }

    return this.submit("patch", url);
  }

  /**
   * Send a DELETE request to the given URL.
   *
   * @param {string} url - The URL to send the request to.
   * @param {number|null} id - The optional ID parameter for the URL.
   */
  delete(url, id = null) {
    if (id) {
      return this.submit("delete", `${url}/${id}`);
    }

    return this.submit("delete", url);
  }

  /**
   * Submit the form.
   *
   * @param {string} requestType - The type of the request.
   * @param {string} url - The URL to send the request to.
   * @returns {Promise} A Promise that resolves with the response data or rejects with the error.
   */
  submit(requestType, url) {
    this.beforeSubmit();

    let data = this.isJson() ? this.jsonData() : this.formData();
    let options = {
      headers: this.getHeaders(),
    };

    return new Promise((resolve, reject) => {
      api[requestType](url, data, options)
        .then((response) => {
          this.onSuccess(response.data);

          resolve(response.data);
        })
        .catch((error) => {
          this.onFail(error.response);

          reject(error.response.data);
        })
        .finally(() => {
          this.afterSubmit();
        });
    });
  }

  /**
   * Reset the form fields to their initial values.
   */
  reset() {
    for (let field in this.$data) {
      this[field] = this.$data[field];
    }

    this.$errors.clear();
  }

  /**
   * Check if an error exists for the given field.
   *
   * @param {string} field - The field name.
   * @returns {boolean} - Returns true if an error exists for the field, false otherwise.
   */
  hasError(field) {
    return this.$errors.has(field);
  }

  /**
   * Get the error message.
   *
   * @returns {string} - The error message.
   */
  getErrorMessage() {
    return this.$errors.getMessage();
  }

  /**
   * Get the error message for a field.
   *
   * @param {string} field - The field name.
   * @returns {any} - The error message for the field.
   */
  getError(field) {
    return this.$errors.get(field);
  }

  /**
   * Clear the error for a specific field or all fields.
   *
   * @param {string|null} field - The optional field name to clear the error for.
   */
  clearError(field = null) {
    return this.$errors.clear(field);
  }

  /**
   * Execute a function before submitting the form.
   */
  beforeSubmit() {
    this.$canSubmit = false;
  }

  /**
   * Execute a function after submitting the form.
   */
  afterSubmit() {
    this.$canSubmit = true;
  }

  /**
   * Handle a successful form submission.
   *
   * @param {object} data - The response data.
   */
  onSuccess(data) {
    if (this.isUpdate()) {
      this.updateData(data, false);
    }

    this.reset();
  }

  /**
   * Handle a failed form submission.
   *
   * @param {object} response - The response object.
   */
  onFail(response) {
    this.$errors.setStatus(response.status);

    if (response.data !== undefined) {
      this.$errors.record(response.data);
    }
  }
}

export default Form;
